body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Loader_loader__1yefo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 3px dashed teal;
    -webkit-animation: Loader_rotate__2FSji 1s infinite linear;
            animation: Loader_rotate__2FSji 1s infinite linear;
}

@-webkit-keyframes Loader_rotate__2FSji {
    from {
        -webkit-transform: rotate(0def) scale(1);
                transform: rotate(0def) scale(1);
    }
    to {
        -webkit-transform: rotate(360deg) scale(1.4);
                transform: rotate(360deg) scale(1.4);
    }
}

@keyframes Loader_rotate__2FSji {
    from {
        -webkit-transform: rotate(0def) scale(1);
                transform: rotate(0def) scale(1);
    }
    to {
        -webkit-transform: rotate(360deg) scale(1.4);
                transform: rotate(360deg) scale(1.4);
    }
}
